*
{
    margin: 0%;
    padding: 0%;
}
.app
{
    text-align: center;
    background-color: aqua;
    padding-bottom: 8%;
}

h1
{
  padding-top: 10px;  
  color: rgb(112, 7, 112);
}
.app input
{
    font-size: 14px;
    border-radius: 5px;
    border: 3px solid grey;
    outline: none;
    padding: 10px 5px;
    width: 200px;
    background-color: rgb(247, 247, 146);
}
.app button
{
    background-color: #1e10e6;
    font-weight: bold;
    color: white;
    margin-top: 10px;
    padding: 10px 30px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 25px;

}
.app-container
{
    background-color: rgb(233, 134, 5);
    color: rgb(30, 5, 139);
    padding: 15px ;
    display: flex;
    justify-content: center;
    margin-top: 5px;
    font-size: 20px;
}

.navActive
{
    color:rgb(90, 6, 72);
    font-size: 30px;
    font-weight: bold;
}
nav
{
    background-color:yellowgreen;
    padding: 10px 0;
    display: flex;
    justify-content: center;
    margin-top: 5px;
}
nav ul
{
    display: flex;
    list-style-type:none;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    color:rgb(129, 6, 6);
    font-weight: bold;
  
}
nav ul li
{
    cursor: pointer;
    font-size: 18px;
   
}
nav ul li:hover
{
    transform: scale(1.0);
}
